div.main {
  text-align: center;
}

div.card {
  border: none;
}

table {
  margin-left: auto;
  margin-right: auto;
}

td {
  background-color: whitesmoke;
  user-select: none;
}

button {
  background-color: gainsboro;
  border: none;
  border-radius: 50vw;
  outline: none;
  opacity: 1;
}

button:active {
  opacity: 0.6;
}

h1, h2, h3, label {
  user-select: none;
}